'use client';
import React, { useState } from 'react';
import { API_BASE, DEV_TOKEN } from '../../../../lib/env';

export default function AccessPage({ params }: { params: Promise<{ id: string }> }) {
  const [pem, setPem] = useState('');
  const [resp, setResp] = useState<string | null>(null);

  async function upload() {
    const org = await params;
    const res = await fetch(`${API_BASE}/api/certs/${org.id}/upload`, {
      method: 'POST',
      headers: { 'content-type': 'application/json', authorization: `Bearer ${DEV_TOKEN}` },
      body: JSON.stringify({ pem }),
    }).then((r) => r.json());
    setResp(JSON.stringify(res));
  }

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">Доступ и роли</h3>
      <div className="border border-gray-800 rounded p-3 bg-gray-900 space-y-2">
        <div className="font-medium">Загрузка X.509 сертификата</div>
        <textarea className="border border-gray-700 rounded p-2 bg-gray-900 w-full h-40" placeholder="-----BEGIN CERTIFICATE-----" value={pem} onChange={(e) => setPem(e.target.value)} />
        <button onClick={upload} className="px-3 py-2 rounded bg-indigo-600 text-white">Upload</button>
      </div>
      {resp && <pre className="text-xs bg-gray-900 border border-gray-800 rounded p-3 whitespace-pre-wrap">{resp}</pre>}
    </div>
  );
}

